import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import { AuthService } from './../auth/auth.service';
import { AlertModalService } from './../alertModal.service';
import { NGXLogger } from 'ngx-logger';


@Injectable({
	providedIn: 'root'
})

// HttpInterceptor 클래스를 확장하면 사용자 정의 인터셉터를 생성하여 서버의 모든 오류 응답을 단일 위치에서 catch 할 수 있습니다.
/* 오류 인터셉터는 api에서 http 응답을 가로 채서 오류가 있는지 확인합니다. 
401 Unauthorized 응답이있는 경우 사용자는 응용 프로그램에서 
자동으로 로그 아웃되고 다른 모든 오류는 호출 서비스에 의해 잡히도록 다시 던져 지므로 경고가 사용자에게 표시 될 수 있습니다.
*/
export class ErrorInterceptor implements HttpInterceptor {

	constructor(private authService: AuthService, private alertModalService: AlertModalService, private logger: NGXLogger) { }


	intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
		return next.handle(request).pipe(retry(1), catchError(err => {

			let data = {};
			let errorMessage = '';
			
			if (err.status === 401) {
				errorMessage = err.status;
				// auto logout if 401 response returned from api
				this.authService.logout();
				return throwError(errorMessage);
			}

			if (err.status === 402 || err.status === 412) { //비밀번호 틀린 경우, 제어 이력에서 취소 실패한 경우 개별 관리
				errorMessage = err.status;
				this.logger.info('errorMessage >>>> ' + errorMessage);
				return throwError(errorMessage);
			}


			if (err.error instanceof ErrorEvent) {
				// client-side error
				data = {
					reason: '동작을 실패하였습니다.',
					status: err.status
				}
			} else {// server-side error	

				if (err.status === 400) { //잘못된 요청, 인자 또는 메시지 타입, 범위 혹은 포맷
					data = {
						reason: '잘못된 요청 입니다. 요청을 다시 확인해 주세요.',
						status: err.status
					}
				} else if (err.status === 403) { // 접근 권한 없음
					data = {
						reason: '해당 페이지의 권한이 없습니다. 관리자에게 문의해 주세요.',
						status: err.status
					}
				} else if (err.status === 404) { // 잘못된 경로 요청 입니다.
					data = {
						reason: '찾는 대상이 없습니다. 다시 확인해주세요.',
						status: err.status
					}
				} else if (err.status === 409) { // 데이터 생성시 중복 ID
					data = {
						reason: '이미 사용중인 ID 입니다. 다시 입력해 주세요.',
						status: err.status
					}
				} else if (err.status === 500) { //내부 서버 오류
					data = {
						reason: '내부 오류 입니다. 관리자에게 문의해 주세요.',
						status: err.status
					}
				} else {
					data = {
						reason: '요청에 실패하였습니다.',
						status: err.status
					}
				}

			}

			this.alertModalService.confirmThis('error', data, function () {
				this.logger.info(err.message);
			}, function () {
				this.logger.info(err.message);
			})

			return throwError(errorMessage);
		}))
	}

}
